.btn {
  background-color: #408F95;
  border: 1px solid #408F95;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-family: "Gotu", sans-serif;
  font-size: 0.875rem;
  font-weight: 400;
  line-height: 1.5714285714;
  max-width: 12.625rem;
  padding: 0.9375rem 2.0625rem;
  position: relative;
  text-align: center;
  transition: 0.2s ease;
  width: 100%;
  z-index: 20;
}

.btn::first-letter {
  text-transform: uppercase;
}

.btn:hover {
  color: #408F95;
}

.btn span {
  background-image: url("../images/common/arrow-right_white.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block;
  height: 0.875rem;
  margin-left: 0.5rem;
  max-width: 2.5rem;
  position: relative;
  transition: 0.2s all;
  width: 100%;
  z-index: 20;
}

.btn:hover span {
  background-image: url("../images/common/arrow-right_green.svg");
}

.btn::before {
  background-color: #fff;
  content: "";
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  width: 100%;
  z-index: -1;
}

.btn:hover::before {
  transform: scale(1, 1);
}

.btn::after {
  content: "";
  background-color: transparent;
  border: 1px solid #408F95;
  height: 100%;
  position: absolute;
  top: 5px;
  left: 5px;
  width: 100%;
  z-index: -10;
}

.btn--wh {
  background-color: #fff;
  border: 1px solid #fff;
  color: #408F95;
  padding: 1rem 2.25rem;
}

.btn--wh:hover {
  color: #fff;
}

.btn--wh span {
  background-image: url("../images/common/arrow-right_green.svg");
}

.btn--wh:hover span {
  background-image: url("../images/common/arrow-right_white.svg");
}

.btn--wh::before {
  background-color: #408F95;
  content: "";
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  width: 100%;
  z-index: -1;
}

.btn--wh:hover::before {
  transform: scale(1, 1);
}

.btn--wh::after {
  content: "";
  background-color: transparent;
  border: 1px solid #fff;
  height: 100%;
  position: absolute;
  top: 5px;
  left: 5px;
  width: 100%;
  z-index: -10;
}

