  padding: 0.5rem 1rem;
  width: 100%;
  /*↓↓選択されているときの色*/
}
[type=text]:hover,
[type=search]:hover,
[type=tel]:hover,
[type=url]:hover,
[type=email]:hover,
[type=password]:hover,
[type=date]:hover,
textarea:hover {
  outline: 2px solid #408F95;
}
[type=text]:focus,
[type=search]:focus,
[type=tel]:focus,
[type=url]:focus,
[type=email]:focus,
[type=password]:focus,
[type=date]:focus,
textarea:focus {
  outline: 2px solid #408F95;
  transition: 0.2s;
}

[type=button],
[type=submit] {
  color: #fff;
  cursor: pointer;
  font-size: 0.875rem;
  line-height: 1;
  padding-inline: 0;
  position: relative;
  transition: 0.2s ease;
  text-transform: capitalize;
  z-index: 20;
}

[type=checkbox] {
  opacity: 0;
  position: absolute;
  left: -100vw;
}
[type=checkbox] + span {
  padding: 0 0 0 1.375rem;
  position: relative;
  font-size: 1rem;
  transition: 0.3s ease;
}
[type=checkbox] + span::before {
  content: "";
  background: #fff;
  border: 1px solid #408F95;
  display: inline-block;
  height: 0.875rem;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 0.875rem;
}
[type=checkbox] + span::before:focus {
  outline: 2px solid #408F95;
  transition: 0.1s ease;
}
[type=checkbox] + span::after {
  opacity: 0;
  transition: 0.1s ease;
}
[type=checkbox]:checked + span::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  display: block;
  opacity: 1;
  /*チェックマークじゃない場合は以下を変更する*/
  transform: translateY(-50%) rotate(-45deg);
  width: 0.7875rem;
  height: 0.4375rem;
  margin-top: -0.1em;
  margin-left: 0.1em;
  border-left: 2px solid #C94800;
  border-bottom: 2px solid #C94800;
}

