  max-width: 90%; /* リストアイテムの幅を制限 */
  margin-top: 0.25rem;
  word-break: break-word; /* 長いテキストがはみ出す場合に改行する */
}

.two-column__sub-content li::before {
  content: "";
  background-image: url("../images/common/seastar_green.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block;
  height: 1rem;
  margin-right: 0.5625rem;
  width: 1rem;
}

.two-column__sub-content li {
  list-style: none;
  display: flex; /* 行頭を揃えるためにflexを使用 */
  align-items: flex-start;
  margin-top: 0.25rem;
  max-width: 90%; /* リストアイテムの幅を制限 */
  word-break: break-word; /* 長いテキストがはみ出す場合に改行する */
  padding-left: 1.5625rem; /* マーカーの幅分だけ左側に余白を追加 */
  position: relative; /* マーカーの位置を相対位置に設定 */
}

.two-column__sub-content li::before {
  content: "";
  background-image: url("../images/common/seastar_green.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block; /* マーカーをブロック要素に変更 */
  height: 1rem;
  margin-right: 0.5625rem;
  width: 1rem;
  position: absolute; /* マーカーを絶対位置に設定 */
  top: 0.3125rem;
  left: 0; /* マーカーをリストアイテムの左端に配置 */
}

.two-column__sub-content .wp-pagenavi {
  gap: 1.5625rem;
  margin-top: 2.5rem;
}
@media screen and (min-width: 768px) {
  .two-column__sub-content .wp-pagenavi {
    margin-top: 5rem;
  }
}

.two-column__mv img {
  height: 100%;
  object-fit: cover;
  width: 100%;
}

.two-column__date {
  font-family: "Gotu", sans-serif;
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 1.2857142857;
}

.two-column__blog-title {
  border-bottom: 1px solid #408F95;
  font-weight: 500;
  font-size: 1.5rem;
  padding-bottom: 0.25rem;
}
@media screen and (min-width: 768px) {
  .two-column__blog-title {
    font-size: 2rem;
    padding-bottom: 0;
  }
}

.two-column__pagenavi {
  margin-top: 4.875rem;
}

.two-column__aside-content {
  margin: 0 auto;
  max-width: 37.5rem;
  width: 85.217391%;
}
@media screen and (min-width: 768px) {
  .two-column__aside-content {
    max-width: 18.375rem;
    width: 100%;
  }
}

.voice-card {
  box-shadow: 2px 2px 10px 0 rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  padding: 1rem 1rem 1.5rem;
}
@media screen and (min-width: 768px) {
  .voice-card {
    padding: 1.5rem;
    width: 100%;
  }
}

.voice-card__body {
  display: flex;
  align-items: end;
  justify-content: space-between;
  gap: 3.389831%;
  width: 100%;
}

.voice-card__header {
  border-bottom: 1px solid #408F95;
  width: 100%;
}

@media screen and (min-width: 768px) {
  .voice-card__meta {
    display: flex;
    align-items: end;
    justify-content: space-between;
  }
}

.voice-card__gender {
  display: block;
  font-size: 0.75rem;
}

.voice-card__title {
  font-size: 1rem;
  font-weight: 500;
  padding-bottom: 0.1875rem;
}
@media screen and (min-width: 768px) {
  .voice-card__title {
    font-size: 1.5rem;
    margin-top: 0.4375rem;
    padding-bottom: 0;
  }
}

.voice-card__img {
  flex-shrink: 0;
  overflow: hidden;
  max-width: 9.4375rem;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .voice-card__img {
    max-width: 11.25rem;
    width: 100%;
  }
}

.voice-card__img img {
  aspect-ratio: 151/117;
  height: 100%;
  object-fit: cover;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .voice-card__img img {
    aspect-ratio: 180/140;
  }
}

@media screen and (min-width: 768px) {
  .voice-card__category.category {
    max-width: 8rem;
    padding: 0.375rem 0.875rem;
  }
}

.voice-card__content {
  margin-top: 1rem;
}
@media screen and (min-width: 768px) {
  .voice-card__content {
    margin-top: 2.5rem;
  }
}

.voice {
  position: relative;
}

.voice::before {
  content: "";
  background-image: url("../images/common/fish-to-right_green.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  height: 14.6666666667vw;
  max-width: 7.5rem;
  position: absolute;
  top: -1.5625rem;
  left: calc(50% - min(46vw, 38.6875rem));
  width: 100%;
  z-index: 1;
}
@media screen and (min-width: 768px) {
  .voice::before {
    top: -4.375rem;
    height: 9.5833333333vw;
    max-width: 18.8125rem;
    z-index: -1;
  }
}

.voice__inner.inner {
  max-width: 37.5rem;
}
@media screen and (min-width: 768px) {
  .voice__inner.inner {
    max-width: 70.625rem;
  }
}

.voice__cards {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  margin-top: 2.5rem;
  place-items: center;
  gap: 2.5rem;
}
@media screen and (min-width: 768px) {
  .voice__cards {
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem 2.5rem;
  }
}

.voice__pagenavi {
  margin-top: 5rem;
}
@media screen and (min-width: 768px) {
  .voice__pagenavi {
    margin-top: 4.5rem;
  }
}

.wrap {
  display: flex;
  flex-direction: column;
}

.wrap--gr {
  background-color: #408F95;
  padding-top: 5rem;
  padding-bottom: 2.5rem;
}
@media screen and (min-width: 768px) {
  .wrap--gr {
    padding-top: 5.625rem;
    padding-bottom: 9.375rem;
  }
}

.wrap--gr .breadcrumbs span {
  color: #fff;
}

.wrap--gr .breadcrumbs span:not(:first-child)::before {
  border-right: 1px solid #fff;
  border-top: 1px solid #fff;
}

.color-box {
  position: relative;
  width: 100%;
}

.color-box .color {
  background-color: #408F95;
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 100%;
  z-index: 2;
}

.color-box img {
  display: block;
  width: 100%;
}

.swiper {
  width: 100%;
  height: auto;
}

.swiper-button-prev,
.swiper-button-next {
  display: inline-block;
  height: 5rem;
  margin-top: 0;
  position: relative;
  top: 0;
  left: 0;
  width: 5rem;
}

.swiper-button-prev::after,
.swiper-button-next::after {
  display: none;
}
@media screen and (min-width: 768px) {
  .swiper-button-prev::after,
  .swiper-button-next::after {
    content: "";
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    cursor: pointer;
    display: inline-block;
    height: 100%;
    position: absolute;
    width: 100%;
  }
}

@media screen and (min-width: 768px) {
  .swiper-button-prev::after {
    background-image: url("../images/common/arrow-left.svg");
  }
}

@media screen and (min-width: 768px) {
  .swiper-button-next::after {
    background-image: url("../images/common/arrow-right.svg");
  }
}

.wp-pagenavi {
  clear: both;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  text-align: center;
}

.wp-pagenavi a,
.wp-pagenavi span {
  border: 1px solid #408F95;
  color: #408F95;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-family: "Lato", sans-serif;
  font-weight: 700;
  height: 2.375rem;
  padding: 0.6875rem 0.9375rem;
  transition: 0.3s ease;
  width: 2.375rem;
}

.wp-pagenavi .page:nth-child(n+6) {
  display: none;
}
@media screen and (min-width: 768px) {
  .wp-pagenavi .page:nth-child(n+6) {
    display: inline-flex;
  }
}

.wp-pagenavi .previouspostslink {
  margin-right: 1.75rem;
  position: relative;
}

.wp-pagenavi .nextpostslink {
  margin-left: 1.75rem;
  position: relative;
}

.wp-pagenavi .previouspostslink::before {
  content: "";
  border-bottom: 1px solid #408F95;
  border-left: 1px solid #408F95;
  height: 0.75rem;
  position: absolute;
  top: 60%;
  left: 45%;
  transform: rotate(45deg) translate(-50%, -50%);
  transform-origin: center;
  width: 0.75rem;
}

.wp-pagenavi .nextpostslink::before {
  content: "";
  border-top: 1px solid #408F95;
  border-right: 1px solid #408F95;
  height: 0.75rem;
  position: absolute;
  top: 60%;
  right: 45%;
  transform: rotate(45deg) translate(-50%, -50%);
  transform-origin: center;
  width: 0.75rem;
}

.wp-pagenavi span.current {
  background-color: #408F95;
  color: #fff;
}

.wp-pagenavi a:hover {
  background-color: #408F95;
  color: #fff;
}

.wp-pagenavi .previouspostslink:hover::before,
.wp-pagenavi .nextpostslink:hover::before {
  border-color: #fff;
  transition: 0.3s ease;
}

.wp-pagenavi .page:first-child {
  margin-left: 1.75rem;
}

.wp-pagenavi .page:last-child {
  margin-right: 1.75rem;
}